}

.logo {
    display: block;
    line-height: 70px;
    width: 260px;
    position: fixed;
    top: 0
}

.logo span.logo-lg {
    display: block
}

.logo span.logo-sm {
    display: none
}

.logo.logo-light {
    display: block
}

.logo.logo-dark {
    display: none
}

.wrapper {
    height: 100%;
    overflow: hidden;
    width: 100%
}

.content-page {
    margin-left: 260px;
    overflow: hidden;
    padding: 70px 12px 65px;
    min-height: 100vh
}

.leftside-menu {
    width: 260px;
    z-index: 10;
    background: linear-gradient(135deg, #8f75da 0, #727cf5 60%);
