@import url(app.min.css);
@import "fontawesome.css";

.menuBar {
    display: none;
}

/*projects css*/
.projectTableWrapper {
    width: 100% !important;
    overflow-x: auto;
}

.projectTableWrapper .react-bootstrap-table {
    min-width: 1041px;
}

/*1px solid #dee2e6*/

/*loader animation css*/
.loaderAnimation {
    width: 70px;
}

/*datePicker css*/
.example-custom-input.bg-transparent {
    border: none;
}

.react-datepicker-wrapper {
    width: 160px !important;
    margin-left: auto !important;
}

.dateLable {
    width: 90px;
}

.dateString {
    border-bottom: 1px solid #aaa;
}

/*project add modal style*/
.modal-body {
    padding: 0px;
    background: #dadaea99;
}

.projectAddModalBodyWrapper {
    margin: 10px;
    padding: 15px;
    background: white;
}

#projectName {
    background: #dadaea99;
}

.form-label {
    color: #2c2c33;
}

.dateString {
    color: #2c2c33;
}


/*expense modal*/

.expenseModal .modal-dialog .modal-content {
    /*width: 1200px;*/
    left: 50%;
    transform: translateX(-50%);
    min-height: 600px;
}

.expenseDateIcon {
    transform: translateX(-3px);

}

.expenseDateString {
    color: rgba(104, 101, 101, 0.91);
    width: 190px;
    border: 2px solid #8181ff;
    font-weight: bolder;
    background: #8181ff;
    color: white;
}

.example-custom-input.bg-transparent {
    width: 176px
}

.expenseCalelnder {
    transform: translateX(-6px);
}

/*print expense talbe*/
.printExpense table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

.printExpense td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

.printExpenseRow {
    border-bottom: 2px solid rgba(0, 0, 0, 0.45) !important;
    text-align: left;
    padding: 8px;
}

.clone3 {
    margin-left: 31px;
}

.clone2 {
    margin-left: 26px;
}

.clone1 {
    margin-left: 24px;
}


/*project reportModal*/
.modal-fullscreen .modal-body {
    overflow-y: auto;
    scrollbar-width: thin;
}

.bg-white.reportContentWreapper {
    width: 82vw;
    min-height: 84vh;
}

.dailyReportToDate {
    transform: translateY(-15px);
}

.dailyReportBillTable {
    background: #dadaea99;
    height: 308px !important;
    overflow-y: auto;
    scrollbar-width: thin;
}

.dailyReportDetailTable {
    background: #dadaea99;
    height: 350px !important;
    overflow-y: auto;
    margin-right: 5px;
    scrollbar-width: thin;
}

.dailyReportDetailCalculation {
    border: none;
}

.dailyReportDetailAmount {
    text-align: right !important;
}


/*update expense table*/
.updateExpense {
    height: 300px;
    background: #80808029;
    overflow-y: scroll;
    scrollbar-width: thin;
    position: relative;
}

.updateExpenseTableHead {
    /*! position: absolute; */
    /*! width: 100%; */
    position: sticky;
    top: 0;
    z-index: 10;
    background: #fff;
}


.reportMainButton {
    position: sticky;
    top: 15px;
}

/*detail report component*/
.detailReportLastExp {
    text-align: right;
}

.detailReportTableDataAlign {
    text-align: right;
}

.detailReportDailyCost {
    border-left: none;
    border-right: 2px solid #0000003d;
}

.detailReportTotalCost {
    border-right: none;
}

.DetailReportCommonRow {
    border-bottom: 3px solid rgb(0, 0, 0);
}

.detailReportTableDataCommonAlign {
    text-align: center;
}
.expenseDetailTable{
    height: 300px;
    overflow-y: auto;
    scrollbar-width: thin;
}
.elementUsesTable{
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: thin;
    height: 300px;
    background: #80808029;
}
